import React, { useState, useRef, useEffect } from 'react';
import './App.css';
import Entry from './components/Scene/Entry/'
import Surveillance from './components/Scene/Surveillance/';
import WindowDimensions from './context/WindowDimensions/';
import setMobileVhUnit from './api/setMobileVhUnit';
import throttle from './api/throttle';

function App({ children }) {
  setMobileVhUnit();

  const dimensions = {
    width: window.innerWidth,
    height: window.innerHeight
  };
  const el = useRef(null);
  const [scroll, setScroll] = useState(0);
  const [scrollEnabled, setScrollEnabled] = useState(false);

  const scrollDown = (length = 1) => setScroll(Math.max(scroll - length, -1 * el.current.childNodes.length + 1));
  const scrollUp = (length = 1) => setScroll(Math.min(scroll + length, 0));

  const handleWheel = (event) => {
    if (scrollEnabled) {
      if (event.deltaY > 0 && scroll > -1 * el.current.childNodes.length) {
        scrollDown();
      } else if (event.deltaY < 0 && scroll <= 0) {
        scrollUp();
      }
    }
  };

  const handleWheelThrottled = throttle(handleWheel, 250);

  useEffect(() => {
    const handleKeyup = (event) => {
      if (scrollEnabled) {
        if (event.code === 'ArrowDown' && scroll > -1 * el.current.childNodes.length) {
          scrollDown();
        } else if (event.code === 'ArrowUp' && scroll <= 0) {
          scrollUp();
        }
      }
    };

    window.addEventListener('keyup', handleKeyup, false);
  });

  return (
    <WindowDimensions.Provider value={dimensions}>
      <div className="vignette" />
      <div
        className="App"
        ref={el}
        onWheel={handleWheelThrottled}
        style={{ transform: `translateY(${scroll * 100}vh)` }}
      >
        <Entry callback={() => {
          setScrollEnabled(true);
          if (scroll === 0) scrollDown();
        }} />
        <Surveillance />
      </div>
    </WindowDimensions.Provider>
  );
}

export default App;
