import React, { useRef, useState, useEffect } from 'react';
import SceneVisible from 'context/SceneVisible/';
import './scene.css';

function Scene({ name, children }) {
  let className = 'scene';

  if (name) {
    className += ` ${name}`;
  }

  const el = useRef(null);
  const [visible, setVisible] = useState(false);
  const [opacity, setOpacity] = useState(0);

  useEffect(() => {
    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          setVisible(true);
        } else {
          setVisible(false);
        }

        setOpacity(entry.intersectionRatio);
      });
    }, { threshold: 0.75 });

    observer.observe(el.current);
  });

  return (
    <SceneVisible.Provider value={visible}>
      <section className={(visible) ? `${className} visible` : className} ref={el}>
        <div className="inner" style={{ opacity: opacity }}>
          {children}
        </div>
      </section>
    </SceneVisible.Provider>
  )
}

export default Scene;
