import React from 'react';
import Scene from 'components/Scene/';
import Button from 'components/Button/';
import './entry.css';

function Entry({ callback = () => { return null; } }) {
  return (
    <Scene name="entry">
      <Button callback={callback}>Join with Computer Audio</Button>
      <p className="textSmall">Welcome to the Remote Experience</p>
    </Scene>
  );
}

export default Entry;
