import React, { useState, useContext, useRef } from 'react';
import WindowDimensions from 'context/WindowDimensions';
import SceneVisible from 'context/SceneVisible';
import { ReactComponent as Eyelid } from './eyelid.svg';
import { ReactComponent as Iris } from './iris.svg';
import useMousePosition from 'api/useMousePosition';
import './eyeball.css';

function Eyeball({ openDelay = 1, twitchDelay = 0 }) {
  const [opened, setOpenedState] = useState(false);
  const { x, y } = useMousePosition();
  const hasPointer = ('matchMedia' in window && matchMedia('(pointer:fine)').matches);
  const windowWidth = useContext(WindowDimensions).width;
  const windowHeight = useContext(WindowDimensions).height;
  const visible = useContext(SceneVisible);
  const irisContainer = useRef(null);

  const map = (value, x1, y1, x2, y2) => (value - x1) * (y2 - x2) / (y1 - x1) + x2;

  return (
    <div
      className="eyeball"
      style={{ animationDelay: `${openDelay}s` }}
      onAnimationEnd={() => setOpenedState(true)}
    >
      <div
        className="eyeflap"
        style={{ animationDelay: `${openDelay}s` }}
      />
      <div
        className={`svgContainer eyelid${(opened) ? '' : ' closed'}`}
        style={{
          animationDelay: `${twitchDelay}s`,
          animationPlayState: (opened) ? '' : 'paused'
        }}
      >
        <Eyelid />
      </div>
      <div className="iris" style={{
        animationDelay: `${twitchDelay}s`,
        animationPlayState: (opened) ? '' : 'paused'
      }}>
        <div
          className="irisContainer"
          ref={irisContainer}
        >
          <div
            className="svgContainer irisController"
            style={{
              transform: (visible && hasPointer) ? `translate(${map(x, 0, windowWidth, -30, 30)}%, ${map(y, 0, windowHeight, -10, 10)}%)` : 'translate(0)'
            }}
          >
            <Iris />
          </div>
        </div>
      </div>

      <div className="sclera" style={{
        animationDelay: `${twitchDelay}s`,
        animationPlayState: (opened) ? '' : 'paused'
      }} />
    </div>
  );
}

export default Eyeball;
